footer {
  background: var(--color-primary);
  padding: 2rem 0;
  text-align: center;
  font-size: 1rem;
  margin-top: 5rem;
}

footer a {
  color: var(--color-bg);
}

.footer__logo {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1.8rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 2rem;
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.footer__socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.8rem;
  border-radius: 1rem;
  display: flex;
  border: 1px solid transparent;
  font-size: 1rem;
}

.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer__copyright {
  color: var(--color-bg);
  font-size: 1.1rem;
  /* margin-bottom: 2rem; */
}


@media screen and (max-width:600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.2rem;
  }

  .footer__socials {
    margin-bottom: 2rem;
  }
}