@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;500;700&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #1f1f38;
  --color-bg-var: #2c2c6c;
  --color-primary: #4196d2;
  --color-primay-var: #2b6a979d;
  --color-white: #fff;
  --color-light: #ffffff80;


  /* container class */
  --container-width-lg: 75%;
  --container-width-md: 85%;
  --container-width-sm: 95%;



  /* transition */
  --transition: all 500ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Oswald', sans-serif;
  /* background: var(--color-bg); */
  color: var(--color-white);
  line-height: 1.5;
  background-color: #313153;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='16' viewBox='0 0 12 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 .99C4 .445 4.444 0 5 0c.552 0 1 .45 1 .99v4.02C6 5.555 5.556 6 5 6c-.552 0-1-.45-1-.99V.99zm6 8c0-.546.444-.99 1-.99.552 0 1 .45 1 .99v4.02c0 .546-.444.99-1 .99-.552 0-1-.45-1-.99V8.99z' fill='%231f1f38' fill-opacity='0.97' fill-rule='evenodd'/%3E%3C/svg%3E");
}



/* general styling */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 7rem;
  padding-top: 7rem;
}

section h2,
section h5 {
  text-align: center;
  color: var(--color-light);
}

section h2 {
  color: var(--color-primary);
  margin-bottom: 1.2rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.7rem 1.2rem;
  cursor: pointer;
  border-radius: 0.5rem;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100;
  object-fit: cover;
}



/* Media Queries */
@media screen and (max-width:1020px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem
  }
}

@media screen and (max-width:600px) {
  .container {
    width: var(--container-width-sm);
  }

  section h2 {
    margin-bottom: 2rem
  }
}